import {FC} from 'react';
import {Pagination, Stack} from "@mui/material";
import {useAppDispatch, useAppSelector} from "store/store";
import {setPage} from "components/app/app.reducer";

export const Footer: FC = () => {
  const page = useAppSelector(state => state.app.page)
  const pagesTotal = useAppSelector(state => state.app.pagesCount)
  
  const dispatch = useAppDispatch()
  
  const setPageHandler = (page: number) => {
    dispatch(setPage(page))
  }
  
  return <Stack spacing={2}>
    <Pagination onChange={(event, page) => setPageHandler(page)} count={pagesTotal} variant="outlined" shape="rounded"
                page={page} defaultPage={1}/>
  </Stack>
}

