import * as React from 'react';
import {FC, memo} from 'react';
import AspectRatio from '@mui/joy/AspectRatio';
import Card from '@mui/joy/Card';
import CardOverflow from '@mui/joy/CardOverflow';
import Typography from '@mui/joy/Typography';
import IconButton from '@mui/joy/IconButton';
import Favorite from '@mui/icons-material/Favorite';
import {useAppDispatch} from "store/store";
import {Delete} from "@mui/icons-material";
import './film.css'
import {deleteFilm, FilmType, likedFilmТС} from "components/films/films.reducer";


type Props = {
  film: FilmType
}

export const Film: FC<Props> = memo(({film}) => {
  const dispatch = useAppDispatch()
  
  const deleteItemHandler = () => {
    dispatch(deleteFilm(film.filmId))
  }
  
  const likeFilmHandler = () => {
    dispatch(likedFilmТС(film.filmId))
  }
  
  return (
    <Card variant="outlined" sx={{width: 300}}>
      <CardOverflow>
        <AspectRatio minHeight={360} maxHeight={250} objectFit="contain">
          <img
            src={film.posterUrl}
            loading="lazy"
            alt={film.nameRu}
          />
        </AspectRatio>
        <IconButton
          className={film.isLiked ? `btn btn_active` : 'btn'}
          aria-label="Like film"
          size="sm"
          variant="soft"
          color="danger"
          sx={{
            position: 'absolute',
            zIndex: 2,
            borderRadius: '50%',
            right: '1rem',
            top: 0,
            transform: 'translateY(50%)',
          }}
          onClick={likeFilmHandler}
        >
          <Favorite/>
        </IconButton>
      
      </CardOverflow>
      <CardOverflow
        variant="soft"
        sx={{
          display: 'flex',
          flexDirection: 'column',
          gap: 1.5,
          height: 180
        }}
      >
        <Typography level="h2" sx={{fontSize: 'md', mt: 2}}>
          {film.nameRu}
        </Typography>
        <Typography level="body2" sx={{mt: 0.5, mb: 2}}>
          <div>
            Жанр: {film.genres.map(el => el.genre).join(', ')}
          </div>
          <div>
          Год выхода: {film.year}
          </div>
          <div>
            Продолжительность: {film.filmLength}
          </div>
        </Typography>
        <IconButton
          className="btn"
          aria-label="Like film"
          size="sm"
          variant="plain"
          color="neutral"
          sx={{
            position: 'absolute',
            zIndex: 2,
            borderRadius: '50%',
            right: '1rem',
            bottom: 30,
            transform: 'translateY(50%)',
          }}
          onClick={deleteItemHandler}
        >
          <Delete/>
        </IconButton>
      </CardOverflow>
    
    
    </Card>
  );
})

