import axios from 'axios'

const instance = axios.create({
  baseURL: 'https://kinopoiskapiunofficial.tech/api/v2.2/',
  headers: {
    "X-API-KEY": "004f80cf-be06-4bab-9714-8e8931ab1f86",
    "Access-Control-Allow-Origin": "*",
    "accept": "application/json",
  }
})

// api
export const filmsApi = {
  getTopFilms(page:string) {
    return instance.get<ResponseType>(`/films/top?type=TOP_250_BEST_FILMS&page=${page}`);
  },
}

// types
type ResponseType = {
  pagesCount: number
  films: FilmResType[]
}
type CountiesType = {
  country: string
}
type GenreType = {
  genre: string
}

export type FilmResType = {
  filmId: number
  nameRu: string
  nameEn: string
  year: string
  filmLength: string
  countries: CountiesType[]
  genres: GenreType[]
  rating: string
  ratingVoteCount: number
  posterUrl: string
  posterUrlPreview: string
  ratingChange: null
}

