const initialState = {
  status: 'idle' as RequestStatusType,
  error: null as string | null,
  pagesCount: 0 as number,
  page: 1 as number
}

export const appReducer = (state: AppInitialStateType = initialState, action: ActionsType): AppInitialStateType => {
  switch (action.type) {
    case 'app/SET_STATUS':
      return {...state, status: action.status}
    case 'app/SET_ERROR':
      return {...state, error: action.error}
    case 'app/SET_PAGES_COUNT':
      return {...state, pagesCount: action.pagesTotal}
    case 'app/SET_PAGE':
      return {...state, page: action.page}
    default:
      return {...state}
  }
}

export const setAppError = (error: string | null) => ({type: 'app/SET_ERROR', error} as const)
export const setAppStatus = (status: RequestStatusType) => ({type: 'app/SET_STATUS', status} as const)
export const setPagesCount = (pagesTotal: number) => ({type: 'app/SET_PAGES_COUNT', pagesTotal} as const)
export const setPage = (page:number) =>({type: 'app/SET_PAGE', page} as const)

export type AppInitialStateType = typeof initialState
export type RequestStatusType = 'idle' | 'loading' | 'succeeded' | 'failed'
type ActionsType =
  ReturnType<typeof setAppError>
  | ReturnType<typeof setAppStatus>
  | ReturnType<typeof setPagesCount>
  | ReturnType<typeof setPage>
