import React, {FC, useState} from 'react';
import {Container, Grid} from "@mui/material";
import {Film} from "components/films/film/Film";
import {useAppDispatch, useAppSelector} from "store/store";
import Button from "@mui/material/Button";
import {setPage} from "components/app/app.reducer";
import './films.css'


export const Films: FC = () => {
  
  const films = useAppSelector(state => state.films.films)
  const favoriteFilms = useAppSelector(state=>state.films.favoriteFilms)
  
  const [showOnlyFavorites, setShowOnlyFavorites] = useState<boolean>(false)
  
  const dispatch = useAppDispatch()
  
  const showFavoriteFilmsHandler = () => {
    setShowOnlyFavorites(true)
  }
  
  const showAllFilmsHandler = () => {
    setShowOnlyFavorites(false)
    dispatch(setPage(1))
  }
  
  let filteredFilms = showOnlyFavorites ? favoriteFilms : films
  return <>
    <div className='buttons'>
      <Button onClick={showAllFilmsHandler} color={'primary'} variant={!showOnlyFavorites? 'contained': "outlined"}>Все фильмы</Button>
      <Button onClick={showFavoriteFilmsHandler} color={'primary'} variant={showOnlyFavorites? 'contained': "outlined"}>Избранные</Button>
    </div>
    <Container maxWidth="xl">
      <Grid container spacing={2} sx={{justifyContent:'center'}}>
        {filteredFilms.map((film) => {
          return (
            <Grid item>
              <Film key={film.filmId.toString()} film={film}/>
            </Grid>
          )
        })}
      </Grid>
    </Container>
  </>
  
}
