import {FilmResType, filmsApi} from "api/api";
import {AppThunk} from "store/store";
import {AxiosError} from "axios";
import {setAppError, setAppStatus, setPagesCount} from "components/app/app.reducer";

const initialState = {
  films: [] as FilmType[],
  favoriteFilms: [] as FilmType[]
}

type InitialStateType = typeof initialState

export const filmsReducer = (state: InitialStateType = initialState, action: ActionsType): InitialStateType => {
  switch (action.type) {
    case 'films/SET_FILMS':
      return {...state, films: action.films.map(f => ({...f, isLiked: false}))}
    case 'films/DELETE_FILM':
      return {...state, films: state.films.filter((f) => f.filmId !== action.id)}
    case "films/LIKED_FILMS":
      return {...state, films: action.films}
    case "films/ADD_TO_FAVORITE":
      return {...state, favoriteFilms: action.films}
    default:
      return {...state}
    
  }
}
export const setFilms = (films: FilmResType[]) => ({type: 'films/SET_FILMS', films} as const)
export const deleteFilm = (id: number) => ({type: 'films/DELETE_FILM', id} as const)
export const likedFilms = (films: FilmType[]) => ({type: 'films/LIKED_FILMS', films} as const)
export const addFilmToFavorite = (films: FilmType[]) => ({type: 'films/ADD_TO_FAVORITE', films} as const)

export const likedFilmТС = (id: number): AppThunk => async (dispatch, getState) => {
  const state = getState();
  const films = state.films.films.map(f => f.filmId === id ? {...f, isLiked: !f.isLiked} : f);
  dispatch(likedFilms(films));
  const favoriteFilms = films.filter(f => f.isLiked);
  dispatch(addFilmToFavorite(favoriteFilms));
};


export const loadFilms = (page: number): AppThunk => async (dispatch) => {
  dispatch(setAppStatus('loading'))
  try {
    let res = await filmsApi.getTopFilms(page.toString())
    dispatch(setFilms(res.data.films))
    dispatch(setAppStatus('succeeded'))
    dispatch(setPagesCount(res.data.pagesCount))
  } catch (err) {
    const errors = err as AxiosError | Error
    setAppError(errors.message)
  } finally {
    dispatch(setAppStatus('idle'))
  }
}

export type FilmType = {
  isLiked: boolean
} & FilmResType

type ActionsType = ReturnType<typeof setFilms>
  | ReturnType<typeof deleteFilm>
  | ReturnType<typeof likedFilms>
  | ReturnType<typeof addFilmToFavorite>
